<template>
  <div class="rank">
    <div class="rank-wrapper">
      <ScrollView>
        <ul>
          <li v-for="(value, key) in category.titles" :key="key">
            <h3 class="group-title">{{value}}</h3>
            <ul class="normal-group" v-if="key === 'officialList'">
              <li v-for="obj in category[key]" :key="obj.id" @click.stop="selectedItem(obj.id)"
              >
                <div class="rank-left">
                  <img v-lazy="obj.Img" alt="">
                  <p>{{obj.update}}</p>
                </div>
                <div class="rank-right">
                  <p v-for="(song, index) in obj.songs" :key="song.first">{{index+1}}.{{song.first}} - {{song.second}}</p>
                </div>
              </li>
            </ul>
            <ul class="other-group" v-else>
              <li v-for="obj in category[key]" :key="obj.id" @click.stop="selectedItem(obj.id)">
                <div class="rank-top">
                  <img v-lazy="obj.Img" alt="">
                  <p>{{obj.update}}</p>
                </div>
                <div class="rank-bottom">
                  <p>{{obj.name}}</p>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </ScrollView>
    </div>
    <transition>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import { getTopListDetail } from '../api/index'
import ScrollView from '../components/ScrollView'
export default {
  name: 'Rank',
  components: {
    ScrollView
  },
  data () {
    return {
      category: {}
    }
  },
  methods: {
    selectedItem (id) {
      this.$router.push(`/rank/detail/${id}/rank`)
    }
  },
  created () {
    getTopListDetail()
      .then((category) => {
        // console.log(category)
        this.category = category
      })
      .catch(function () {
        console.log('排行版数据获取失败')
      })
  }
}
</script>

<style scoped lang="scss">
@import "../assets/css/variable";
@import "../assets/css/mixin";
.rank{
  width: 100%;
  height: 100%;
  .rank-wrapper{
    position: fixed;
    left: 0;
    right: 0;
    top: 184px;
    bottom: 0;
    overflow: hidden;
    padding-bottom: 150px;
    @include bg_sub_color();
    .group-title{
      font-weight: bold;
      padding: 10px 20px;
      @include font_color();
      @include font_size($font_large);
    }
    .normal-group{
      width: 100%;
      li{
        display: flex;
        align-items: center;
        padding: 10px 20px;
        box-sizing: border-box;
        .rank-left{
          position: relative;
          img{
            width: 200px;
            height: 200px;
            border-radius: 10px;
          }
          p{
            position: absolute;
            left: 10px;
            bottom: 10px;
            color: white;
            @include font_size($font_medium_s)
          }
        }
        .rank-right{
          width: 100%;
          margin-left: 20px;
          P{
            width: 90%;
            line-height: 45px;
            @include clamp(1);
            @include font_color();
            @include font_size($font_samll);
          }
        }
      }
    }
    .other-group{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      li{
        padding: 10px 20px;
        box-sizing: border-box;
        .rank-top{
          position: relative;
          img{
            width: 200px;
            height: 200px;
            border-radius: 10px;
          }
          p{
            position: absolute;
            left: 10px;
            bottom: 10px;
            color: white;
            @include font_size($font_medium_s)
          }
        }
        .rank-bottom{
          width: 200px;
          //@include no-wrap();
          P{
            line-height: 45px;
            padding: 10px 0;
            text-align: center;
            @include font_color();
            @include font_size($font_samll);
            @include clamp(1);
          }
        }
      }
    }
  }
}
.v-enter{
  transform: translateX(100%);
}
.v-enter-to{
  transform: translateX(0%);
}
.v-enter-active{
  transition: transform 0.5s;
}
.v-leave{
  transform: translateX(0%);
}
.v-leave-to{
  transform: translateX(100%);
}
.v-leave-active{
  transition: transform 0.5s;
}
</style>
